<template>
    <div class="login">
      <div>
        <h1>{{ msg }}</h1>
        <h2>Please login</h2>
        Name: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="username" type="text" name = "userName"/>
        <div><br /></div>
        Password: <input  v-model="password" type="password" name="password"/>
        <div><br /></div>
        <button v-bind:disabled="!username || !password" v-on:click="login">登录</button>
        <p v-if="!password || !username" style="color: red">请输入用户名密码</p>
      </div>
    </div>
</template>

<script>

export default {
  name: 'Login',

  data () {
    return {
      msg: 'Welcome to Sign In App',
      username: '',
      password: ''
    }
  },
  methods: {
    login () {
      const that = this
      this.$http.post('http://localhost:9000/login',
        JSON.stringify({id: 1, userName: this.username, password: this.password})).then(res => {
        if (!res.data) {
          alert('账号密码错误')
          this.clean()
        } else {
          that.$router.push({name: 'team'})
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    clean () {
      this.username = ''
      this.password = ''
    }
  }
}
</script>

<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
</style>



