<template>
  <div id="team">
    <h1>创建签到</h1>
    <div>title: &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<input v-model="title"/></div>
    <div>check_num：<input v-model="check"/></div>
    <button v-bind:disabled="!title || !check" v-on:click="teama">创建</button>
    <p v-if="!check" style="color: red">请输入签到验证码</p>
  </div>
</template>

<script>
export default {
  name: 'Team',
  data () {
    return {
      title: '',
      checkNum: '',
      check: ''
    }
  },
  methods: {
    teama () {
      const that = this
      this.$http.post('http://192.168.137.1:9000/team',
        JSON.stringify({title: this.title, checkNum: this.check}), {ContentType: 'application/json'}).then(res => {
        if (!res.data) {
          alert(res.data.message)
          this.clean()
        } else {
          that.$router.push({name: 'start', params: {data: res.data.team.id, ccnum: res.data.team.checkNum}})
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    clean () {
      this.check = ''
    }
  }
}
</script>



