<template>
    <div>
      <div><span style="color: blue">签到地址:</span>{{msg}}</div>
      <div><span style="color: blue">验证码:</span>{{num}}</div>
      <button v-bind:disabled="end" v-on:click="start1">开始</button>
      <button v-bind:disabled="sta" v-on:click="stop1">结束</button>
      <button v-bind:disabled="sta" v-on:click="stop1">生成excle</button>
  </div>
</template>

<script>
export default {
  name: 'Start',
  data () {
    return {
      end: false,
      sta: true,
      id: null,
      msg: 'http://192.168.137.1:9000/sign?team=' + this.$route.params.data,
      num: this.$route.params.ccnum
    }
  },
  methods: {
    start1 () {
      this.id = this.$route.params.data
      this.$http.post('http://192.168.137.1:9000/start',
        JSON.stringify({id: this.id})).then(res => {
        if (!res.data) {
          alert('启动失败请重新启动')
        } else {
          this.end = true
          this.sta = false
        }
      }).catch(function (err) {
        console.log(err)
      })
    },
    stop1 () {
      this.$http.post('http://192.168.137.1:9000/end',
        JSON.stringify({id: this.$route.params.data})).then(res => {
        if (!res.data) {
          alert('停止失败请重新停止')
        } else {
          this.end = false
          this.sta = true
        }
      }).catch(function (err) {
        console.log(err)
      })
    }
  }

}
</script>

<style scoped>

</style>



