import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Team from '@/components/Team'
import Start from '@/components/Start'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/Team',
      name: 'team',
      component: Team
    },
    {
      path: '/Start',
      name: 'start',
      component: Start
    }
  ]
})



